import { EventEmitter, Injectable, Output } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { JwtTokenService } from './jwt-token.service';
import { StoreTokenService } from './store-token.service';

@Injectable({
  providedIn: 'root'
})
export class CommonService {
  title = new BehaviorSubject('Plastic Container');
  
  constructor(private token: StoreTokenService, private jwt:JwtTokenService) { 
  }

  setTitle(title: string){
    this.title.next(title);
  }

  getUser(){

    return ({
      'userId': this.token.get('id'),
      'name': this.token.get('name'),
      'code': this.token.get('code')
    });
  }

  envUrl(){
    return window['myValue'];
  }

  decToBin(n): any{
    var bin = (+n).toString(2);
    let count = 10 - bin.length
    let zero = '0';
    bin = zero.repeat(count) +bin
    return bin;
  }
}
