import { Component, OnInit } from "@angular/core";
import { PartyGuard } from "app/@theme/guards/party.guard";
import { PlaceOrderGuard } from "app/@theme/guards/plce-order.guard";
import { QualityGuard } from "app/@theme/guards/quality.guard";
import { MENU_ITEMS } from "./pages-menu";
@Component({
  selector: "ngx-pages",
  styleUrls: ["pages.component.scss"],
  template: `
    <ngx-one-column-layout>
      <nb-menu [items]="menu"></nb-menu>
      <router-outlet></router-outlet>
    </ngx-one-column-layout>
  `,
})
export class PagesComponent implements OnInit {
  menu = MENU_ITEMS;
  view: Boolean = true;
  view_all: Boolean = true;
  view_group: Boolean = true;
  constructor(public partyGuard: PartyGuard, private placeOrderGuard: PlaceOrderGuard) {}
  ngOnInit(): void {
    this.menu.forEach((e) => {
      switch (e.title) {
        case "Party":
          this.view = this.partyGuard.accessRights();
          if (!this.view) e.hidden = true;
          else e.hidden = false;
          break;

          case "Place Order":
            this.view = this.placeOrderGuard.accessRights();
            if (!this.view) e.hidden = true;
            else e.hidden = false;
            break;
      }
    });
  }
}
