import { Component, Input, OnDestroy, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import {
  NbMediaBreakpointsService,
  NbMenuService,
  NbSidebarService,
  NbThemeService,
} from "@nebular/theme";
import { CommonService } from "app/@theme/services/common.service";
import { OrderService } from "app/@theme/services/order.service";
import { StoreTokenService } from "app/@theme/services/store-token.service";
import { ToastrService } from "ngx-toastr";
import { Subject, Subscription } from "rxjs";
import { filter, map, takeUntil } from "rxjs/operators";
import { UserData } from "../../../@core/data/users";
import { LayoutService } from "../../../@core/utils";

@Component({
  selector: "ngx-header",
  styleUrls: ["./header.component.scss"],
  templateUrl: "./header.component.html",
})
export class HeaderComponent implements OnInit, OnDestroy {

  private destroy$: Subject<void> = new Subject<void>();
  userPictureOnly: boolean = false;
  user: any;
  imgpath: any = "../../../../assets/img/profile.png";
  currentUser$: Subscription;
  currentTheme = "default";
  currentUser;
  userName;
  userMenu = [ { title: "Log out" }];
  isAdmin: boolean = false;
  newOrders:number = 0;
  cartCount:number = 0;
  headerTitle:string = '';

  constructor(
    private sidebarService: NbSidebarService,
    private menuService: NbMenuService,
    private themeService: NbThemeService,
    private route: Router,
    private userService: UserData,
    private layoutService: LayoutService,
    private tokenService: StoreTokenService,
    private breakpointService: NbMediaBreakpointsService,
    private commonService: CommonService,
    private orderService: OrderService,
  ) {
    this.commonService.title.subscribe(updatedTitle => {
      this.headerTitle = updatedTitle;
    });
    let uName = this.commonService.getUser().name;
    if (uName == "ADMIN"){
      this.isAdmin = true;
    }
    else{
      this.isAdmin = false;
    }
    this.getNewOrders(); 
    this.getCartCount();  
  }

  ngOnInit() {
    let uName = this.commonService.getUser().name;
    if (this.isAdmin){
      this.isAdmin = true;
      setInterval(() => {
        this.getNewOrders();  
      }, 2000);
    } 
    else{
      this.isAdmin = false;
      setInterval(() => {
        this.getCartCount();  
      }, 2000);
    }
    this.currentTheme = this.themeService.currentTheme;
    this.userName = this.commonService.getUser().name;
    this.menuService
      .onItemClick()
      .pipe(
        filter(({ tag }) => tag === "my-context-menu"),
        map(({ item: { title } }) => title)
      )
      .subscribe((title) => {
        if (title === "Log out") this.logout();
      });

    this.userService
      .getUsers()
      .pipe(takeUntil(this.destroy$))
      .subscribe((users: any) => (this.user = users.nick));

    const { xl } = this.breakpointService.getBreakpointsMap();
    this.themeService
      .onMediaQueryChange()
      .pipe(
        map(([, currentBreakpoint]) => currentBreakpoint.width < xl),
        takeUntil(this.destroy$)
      )
      .subscribe(
        (isLessThanXl: boolean) => (this.userPictureOnly = isLessThanXl)
      );

    this.themeService
      .onThemeChange()
      .pipe(
        map(({ name }) => name),
        takeUntil(this.destroy$)
      )
      .subscribe((themeName) => (this.currentTheme = themeName));
  }

  getNewOrders(){
    this.orderService.getNewOrdersCount().subscribe(
      data=>{
        if(data['success']){
          this.newOrders = data['data']
        }else{
          this.removeBadgeIfzero()
          this.newOrders = 0;
        }
      },
      error=>{
        this.newOrders = 0;
        this.removeBadgeIfzero()
       // this.toastr.error('Internal server error')
      }
    )
  }

  getCartCount(){
    this.orderService.getCartItemCount(this.commonService.getUser().userId).subscribe(
      data=>{
        if(data['success'])
        this.cartCount = data['data'];
        else{
          this.removeBadgeIfzero()
          this.cartCount = 0;
        }
      },
      error=>{
        this.removeBadgeIfzero();
        this.cartCount = 0;
      }
    )
  }

  removeBadgeIfzero(){
    const badges = document.getElementsByClassName('status-info');
    for (let i = 0; i < badges.length; i++) {
        const badge = badges[i] as HTMLElement;
        badge.style.display = "none";
    }
  }

  logout() {
    //remove tokens from localstorage. and redirect to login.
    this.tokenService.remove("name");
    this.tokenService.remove("id");
    this.tokenService.remove("code");
    this.route.navigate(["auth"]);
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }

  changeTheme(themeName: string) {
    this.themeService.changeTheme(themeName);
  }

  toggleSidebar(): boolean {
    this.sidebarService.toggle(true, "menu-sidebar");
    this.layoutService.changeLayoutSize();

    return false;
  }

  navigateHome() {
    this.menuService.navigateHome();
    return false;
  }

  goToCart() {
    this.route.navigate(["pages/cart"]);
  }

  goToNewOrders(){
    if(this.isAdmin)
      this.route.navigate(["pages/newOrder"]);
  }
}
