import { Injectable } from "@angular/core";
import {
  ActivatedRouteSnapshot,
  CanActivate,
  Router,
  RouterStateSnapshot,
  UrlTree,
} from "@angular/router";
import * as errorData from "app/@theme/json/error.json";
import { ToastrService } from "ngx-toastr";
import { Observable } from "rxjs";
import { AuthService } from "../services/auth.service";
import { CommonService } from "../services/common.service";
import { JwtTokenService } from "../services/jwt-token.service";
import { StoreTokenService } from "../services/store-token.service";

@Injectable({
  providedIn: "root",
})
export class PartyGuard implements CanActivate {
  public errorData: any = (errorData as any).default;
  permis: String;
  constructor(
    private commonService: CommonService,
    private jwtToken: JwtTokenService,
    private storeTokenService: StoreTokenService,
    private toastr: ToastrService,
    private _router: Router,
    private auth: AuthService
  ) {}

  canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ):
    | Observable<boolean | UrlTree>
    | Promise<boolean | UrlTree>
    | boolean
    | UrlTree {
    this.permis = this.storeTokenService.get("name");
    if (this.permis == "ADMIN") return true;
    else {
      this._router.navigate(["/pages"]);
      return false;
    }
  }

  canLoad(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ):
    | Observable<boolean | UrlTree>
    | Promise<boolean | UrlTree>
    | boolean
    | UrlTree {
    this.permis = this.storeTokenService.get("name");
    if (this.permis == "ADMIN") return true;
    else {
      this._router.navigate(["/pages"]);
      return false;
    }
  }

  accessRights():Boolean{
    let permission = this.storeTokenService.get('name')
    if(permission == 'ADMIN')
      return true;
    else
      return false;
  }
}
