import { NgModule } from "@angular/core";
import { PreloadAllModules, RouterModule, Routes } from "@angular/router";
import { AuthGuard } from "./@theme/guards/auth.guard";
import { PartyGuard } from "./@theme/guards/party.guard";
import { QualityGuard } from "./@theme/guards/quality.guard";
import { StopAuthGuard } from "./@theme/guards/stop-auth.guard";
import { NotFoundComponent } from "./pages/miscellaneous/not-found/not-found.component";
import { PagesComponent } from "./pages/pages.component";

export const routes: Routes = [
  {
    path: "auth",
    loadChildren: () => import("./auth/auth.module").then((m) => m.AuthModule),
    canActivate: [StopAuthGuard],
  },
  {
    path: "pages",
    component: PagesComponent,
    canActivate: [AuthGuard],
    children: [
      {
        path: "cart",
        loadChildren: () =>
          import("./pages/cart/cart.module").then((m) => m.CartModule),
      },
      {
        path: "newOrder",
        loadChildren: () =>
          import("./pages/new-order/new-order.module").then((m) => m.NewOrderModule),
      },
      {
        path: "finishedMeter",
        loadChildren: () =>
          import("./pages/finished-meter/finished-meter.module").then(
            (m) => m.FinishedMeterModule
          ),
      },
      {
        path: "miscellaneous",
        loadChildren: () =>
          import("./pages/miscellaneous/miscellaneous.module").then(
            (m) => m.MiscellaneousModule
          ),
      },
      {
        path: "party",
        loadChildren: () =>
          import("./pages/add-customer/add-customer.module").then(
            (m) => m.AddCustomerModule
          ),
        canActivate: [PartyGuard],
        canLoad: [PartyGuard],
      },
      {
        path: "order",
        loadChildren: () =>
          import("./pages/place-order/place-order.module").then(
            (m) => m.PlaceOrderModule
          ),
      },
      {
        path: "product",
        loadChildren: () =>
          import("./pages/add-product/add-product.module").then(
            (m) => m.AddProductModule
          ),
      },
      {
        path: "",
        redirectTo: "order",
        pathMatch: "full",
      },
      {
        path: "**",
        component: NotFoundComponent,
      },
    ],
  },
  { path: "", redirectTo: "auth", pathMatch: "full" },
  { path: "**", redirectTo: "auth" },
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, {
      preloadingStrategy: PreloadAllModules,
      scrollPositionRestoration: "enabled",
      anchorScrolling: "enabled",
      useHash: false,
    }),
  ],
  exports: [RouterModule],
})
export class AppRoutingModule {}
