import { HttpClient } from '@angular/common/http';
import { Injectable, Output } from '@angular/core';
import { EventEmitter } from 'events';
import { CommonService } from './common.service';

@Injectable({
  providedIn: 'root'
})
export class OrderService {

  constructor(private httpClient:HttpClient,private commonService:CommonService) { 
  }

  deleteOrder(id){
    return this.httpClient.delete(this.commonService.envUrl()+'deleteById/'+id);
  }

  saveFromCart(data){
    return this.httpClient.put(this.commonService.envUrl()+'saveFromCart',data);
  }

  updateStatus(id,status){
    return this.httpClient.get(this.commonService.envUrl()+'updateStatus/'+id+'/'+status);
  }

  getAllOrders(){
    return this.httpClient.get(this.commonService.envUrl()+'getAllOrder');
  }

  getAllCapacity(){
    return this.httpClient.get(this.commonService.envUrl()+'getAllCapacity');
  }

  getAllQuality(){
    return this.httpClient.get(this.commonService.envUrl()+'getAllQuality');
  }

  getAllType(){
    return this.httpClient.get(this.commonService.envUrl()+'getAllType');
  }

  getAllPackageSize(){
    return this.httpClient.get(this.commonService.envUrl()+'getAllPackageSize');
  }

  addToCart(data){
    return this.httpClient.post(this.commonService.envUrl()+'addToCart',data);
  }

  getById(id){
    return this.httpClient.get(this.commonService.envUrl()+'getOrder/byId/'+id);
  }
  
  placeOrder(data): any{
    return this.httpClient.post(this.commonService.envUrl()+'order',data);
  }

  getOrderListByPartyId(id): any{
    return this.httpClient.get(this.commonService.envUrl()+'order/byPartyId/'+id);
  }

  getOrderListByPartyIdForCart(id): any{
    return this.httpClient.get(this.commonService.envUrl()+'order/byPartyId/fromCart/'+id);
  }

  updateOrder(data){
    return this.httpClient.put(this.commonService.envUrl()+'updateOrder',data);
  }

  getNewOrders(){
    return this.httpClient.get(this.commonService.envUrl()+'notify/newOrders');
  }

  getNewOrdersCount(){
    return this.httpClient.get(this.commonService.envUrl()+'notify/newOrders/count');
  }

  getCartItemCount(id){
    return this.httpClient.get(this.commonService.envUrl()+'notify/cart/count/'+id);
  }
}
